<template>
    <div class="information">
        <Header title=""/>
        <div class="Header"></div>
        <van-field v-model="userInfo.phone" name="" label="" disabled placeholder=""/>
        <van-field v-model="userInfo.upMerchant" name="" label="" disabled placeholder=""/>
        <van-field v-model="userInfo.nickname" name="" label="" placeholder=""/>
        <van-field v-model="userInfo.weixin" name="" label="" placeholder=""/>
        <van-field v-model="userInfo.qq" name="" label="QQ" placeholder="QQ"/>
        <van-cell title="" is-link @click="$router.push({path: '/Personal/ChangePassword'});"/>
        <div class="Btn" @click="onSubmit"></div>
    </div>
</template>
<script>
    import Header from "@/Dpage/Components/Header/index";
    import {mapActions, mapState} from "vuex";

    export default {
        components: {
            Header
        },
        data() {
            return {
                WX: "",
                QQ: "",
                username: ""
            };
        },
        computed: {
            ...mapState('user', {
                userInfo: state => state.userInfo,
            })
        },
        methods: {
            ...mapActions('user', ["updateInfo2"]),
            onSubmit() {
                this.updateInfo2({
                    nickname: this.userInfo.nickname,
                    upMerchant: this.userInfo.upMerchant,
                    weixin: this.userInfo.weixin,
                    qq: this.userInfo.qq,
                }).then(response => {
                    const { msg} = response
                    this.$toast(msg);
                }).catch(() => {
                })
            }
        }
    };
</script>
<style lang="less" scoped>
    .information {
        .Header {
            line-height: 32px;
            color: rgba(255, 255, 255, 1);
            padding-left: 15px;
            width: 375px;
            height: 32px;
            background: linear-gradient(
                    180deg,
                    rgba(255, 101, 9, 1) 0%,
                    rgba(255, 128, 53, 1) 100%
            );
        }

        .Btn {
            color: rgba(255, 255, 255, 1);
            width: 280px;
            height: 40px;
            background: rgba(255, 101, 9, 1);
            border-radius: 8px;
            margin: 0 auto;
            text-align: center;
            line-height: 40px;
            margin-top: 44px;
        }
    }
</style>